import api from "@/api"

export default {
    state: {
        basket: []
    },
    mutations: {
        setBasketData(state, products) {
            state.basket = products
        },
        addProductToBasket(state, product) {
            state.basket.push(product)
        },
        removeProductFromBasket(state, productId) {
            const index = state.basket.findIndex(i => i.id === productId)
            state.basket.splice(index, 1)
        },
        changeProductQuantity(state, {productId, quantity}) {
            const index = state.basket.findIndex(i => i.id === productId)
            state.basket[index].quantity = quantity
        },
        clearBasket(state) {
            state.basket.length = 0
            console.log(state.basket)
        }
    },
    actions: {
        async fetchBasket({commit}) {
            const {products} = await api.getBasket()
            commit('setBasketData', products)
        },
        async addProductToBasket({commit}, product) {
            const data = await api.addProductToBasket(product.id)
            commit('addProductToBasket', {
                product,
                quantity: data.quantity,
                id: data.id
            })
        },
        async removeProductFromBasket({commit}, productId) {
            await api.removeProductFromBasket(productId)
            commit('removeProductFromBasket', productId)
        },
        async changeProductQuantity({commit}, {productId, quantity}) {
            await api.changeProductQuantity(productId, quantity)
            commit('changeProductQuantity', {productId, quantity})
        },
        async submit({commit}, data) {
            await api.submitBasket(data)
            commit('clearBasket')
        }
    },
    getters: {
        basket: state => state.basket,
        totalPrice: state => state.basket.reduce((acc, item) => {
            return acc + item.product.price*item.quantity
        }, 0),
        totalProducts: state => state.basket.length
    }
}