export default {
    state: {
        isActive: false
    },
    mutations: {
        openPopup(state) {
            state.isActive = true
        },
        closePopup(state) {
            state.isActive = false
        }
    },
    actions: {
        openPopup({commit}) {
            commit('openPopup')
        },
        closePopup({commit}) {
            commit('closePopup')
        }
    },
    getters: {
        isPopupOpened: state => state.isActive
    }
}