<template>
  <div id="app">
    <Header v-if="this.$vssWidth > 1170" :theme="theme"></Header>
    <Nav v-if="this.$vssWidth > 1170" :theme="theme"></Nav>

    <MobileHeader v-if="this.$vssWidth <= 1170" :theme="theme"></MobileHeader>

    <router-view></router-view>

    <Footer v-if="this.$vssWidth > 1170"></Footer>
    <Nav :footer="true" v-if="this.$vssWidth > 1170"></Nav>

    <MobileFooter v-if="this.$vssWidth <= 1170"></MobileFooter>

    <Law></Law>

    <NotificationsGroup></NotificationsGroup>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Nav from "@/components/Nav";
import Footer from "@/components/Footer";
import Law from "@/components/Law";
import MobileHeader from "@/components/MobileHeader";
import MobileFooter from "@/components/MobileFooter";
import NotificationsGroup from "@/components/NotificationsGroup";

export default {
  name: 'App',
  components: {MobileFooter, Nav, Header, Footer, Law, MobileHeader, NotificationsGroup},
  computed: {
    theme() {
      return this.$route.meta.theme || 'dark'
    }
  },
  created() {
    this.$store.dispatch('fetchBasket')
  }
}
</script>

