import axios from 'axios'

const api = axios.create()

api.defaults.xsrfCookieName = 'csrftoken'
api.defaults.xsrfHeaderName = "X-CSRFTOKEN"

const getBrands = async (category) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/brands/${category || ''}`,
        method: 'GET'
    })
    return data
}

const getBasket = async () => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/basket/`,
        method: 'GET'
    })
    return data
}

const getShopCategories = async () => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/shop/categories/`,
        method: 'GET'
    })
    return data
}

const getProducts = async (categoryId, page) => {
    let url = `${process.env.VUE_APP_SERVER_URL}/api/v1/shop/products/${categoryId}`
    if (page) url += `?page=${page}`
    const {data} = await api({
        url,
        method: 'GET'
    })
    return data
}

const addProductToBasket = async (productId) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/basket/product/${productId}`,
        method: 'POST'
    })
    return data
}

const removeProductFromBasket = async (productId) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/basket/product/${productId}`,
        method: 'DELETE'
    })
    return data
}

const changeProductQuantity = async (productId, quantity) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/basket/product/${productId}`,
        method: 'PUT',
        data: {quantity}
    })
    return data
}

const submitBasket = async (data) => {
    const {result} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/order/`,
        method: 'POST',
        data
    })
    return result
}

const getCases = async (category) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/cases/${category || ''}`,
        method: 'GET'
    })
    return data
}

const getMasters = async (category) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/masters/${category || ''}`,
        method: 'GET'
    })
    return data
}

const getReviews = async (category) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/reviews/${category || ''}`,
        method: 'GET'
    })
    return data
}

const getHomeProducts = async () => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/shop/products/home/`,
        method: 'GET'
    })
    return data
}

const getServiceCategories = async (category) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/services/categories/${category || ''}`,
        method: 'GET'
    })
    return data
}

const getServicesForCategory = async (categoryId) => {
    const {data} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/services/items/${categoryId}`,
        method: 'GET'
    })
    return data
}

const submitForm = async (data) => {
    const {result} = await api({
        url: `${process.env.VUE_APP_SERVER_URL}/api/v1/application/`,
        method: 'POST',
        data
    })
    return result
}

export default {
    getBrands,
    getBasket,
    getShopCategories,
    getProducts,
    addProductToBasket,
    removeProductFromBasket,
    changeProductQuantity,
    submitBasket,
    getCases,
    getMasters,
    getReviews,
    getHomeProducts,
    getServiceCategories,
    getServicesForCategory,
    submitForm
}