import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            component: () => import('../views/Home.vue'),
            meta: {
                title: 'Главная',
                theme: 'dark'
            }
        },
        {
            path: '/haircut',
            name: 'haircut',
            component: () => import('../views/Haircuts.vue'),
            meta: {
                title: 'Парикмахерские услуги',
                theme: 'dark'
            }
        },
        {
            path: '/nails',
            name: 'nails',
            component: () => import('../views/Nails.vue'),
            meta: {
                title: 'Маникюр',
                theme: 'dark'
            }
        },
        {
            path: '/eyebrows',
            name: 'eyebrows',
            component: () => import('../views/Eyebrows.vue'),
            meta: {
                title: 'Брови и ресницы',
                theme: 'dark'
            }
        },
        {
            path: '/shop',
            name: 'shop',
            component: () => import('../views/Shop.vue'),
            meta: {
                title: 'Магазин',
                theme: 'dark'
            }
        },
        {
            path: '/basket',
            name: 'basket',
            component: () => import('../views/BasketPage.vue'),
            meta: {
                title: 'Корзина',
                theme: 'light'
            }
        },
        {
            path: '/reviews',
            name: 'reviews',
            component: () => import('../views/Reviews.vue'),
            meta: {
                title: 'Отзывы',
                theme: 'light'
            }
        },
        {
            path: '/contacts',
            name: 'contacts',
            component: () => import('../views/Contacts.vue'),
            meta: {
                title: 'Контакты',
                theme: 'light'
            }
        },
    ],
    scrollBehavior() {
        return {x: 0, y: 0}
    }
})
